.filterBox {
  display: flex;
  justify-content: center;
  flex-direction: column;
  min-width: 232px;
  margin-right: 20px;
  background-color: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}
@media (max-width: 767px) {
  .filterBox {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.filterBox__name {
  padding: 20px 20px 8px 20px;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #4a4a4a;
}
.filterBox__list {
  font-weight: 400;
}
